.App {
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  background-color: rgb(0, 182, 170);
}

#posts {
  text-align: left;
  margin: 3em;
}


img {
  margin: 3em;
}

.postItem {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 1em;
  border: 5px solid rgb(0, 182, 170);
  background-color: rgb(250, 250, 250);
}

.postDate {
  font-size: 0.75em;
  color: gray;
  margin-top: 0.15em;
}

.postText {
  font-size: 1.5em;
}

.App-header {
  background-color: rgb(0, 133, 124);
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}
.allMessages {
  display: -ms-flexbox;
  display: flex;
  text-align: left;
}
.allMessages ul {
  list-style-type: none;
}

.ListItem {
  width: 200px;
  height: 200px;
}



